/* //BUTTON TOGGLER CSS */
.switch {
    position: relative;
    display: inline-block;
    width: 8rem;
    height: 2.2rem;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0px 1.5rem;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  .round {
      border-radius: 30rem;
  }
  .slider.round:before {
    border-radius: 50%;
  }
  .slider:before {
    position: absolute;
    content: "";
    height: 80%;
    width: 40%;
    left: 2px;
    top: 10%;
    bottom: 10%;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(2.6rem);
    -ms-transform: translateX(2.6rem);
    transform: translateX(2.6rem);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 30rem;
  }
/* //BUTTON TOGGLER CSS ENDS HERE   */

/* MAIN SITE CSS LANDING PAGE BEGINFGS HERE */
.starter {
