    outline: none;
    color: white;
    margin: 1rem 0px;
    border: 1px solid green;
    border-radius: 1rem;
    font-size: 2rem;
  }
}
.btn {
  box-shadow: .2rem .2rem 2rem rgba(0, 0, 0, 0.493);
  &:hover {
    transform: translateY(-2px);
    box-shadow: .2rem .5rem 2rem rgba(0, 0, 0, 0.493);
    cursor: pointer;
  }
  &:active {
    transform: translateY(-1px);
  }
}
